<script>
import {
  fetchGetServiceList,
  fetchGetInstanceList,
  fetchGetPfcp,
} from "@/api/demo/index";
export default {
  name: "pfcp",
  data() {
    return {
      serviceName: "",
      serviceOptions: [],
      instanceOptions: [], //upf下面的实例
      instanceValue: "", //当前选中的实例
      isFirst: true,
      loading: false,
      serverData: {},
    };
  },
  mounted() {
    this.getService();
  },
  methods: {
    // 获取服务列表
    getService() {
      fetchGetServiceList({
        pageNo: 1,
        pageSize: 50,
      })
        .then((response) => {
          console.log(response);
          this.serviceOptions = response.doms.filter(
            (item) => !item.startsWith("ace")
          );
          if (this.serviceOptions.length > 0) {
            this.serviceName = this.serviceOptions[0];
            this.getServiceInstance(this.serviceName);
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 选择服务
    changeService(val) {
      if (!val) {
        return;
      }
      this.getServiceInstance(val);
    },
    // 选择实例
    changeInstance(val) {
      // 图表置空
      this.serverData = [];

      // 获取告警数据
      this.getPfcp();
    },
    //获取服务下面的实例
    getServiceInstance(serviceName) {
      fetchGetInstanceList({ serviceName: serviceName })
        .then((res) => {
          this.instanceOptions = res.hosts;
          if (this.instanceOptions.length > 0) {
            this.instanceValue = this.instanceOptions[0].instanceId;
            this.changeInstance(this.instanceValue);
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 获取数据
    getPfcp() {
      this.loading = true;
      fetchGetPfcp({
        request_type: "pfcp perform",
        instance_id: this.instanceValue,
        sequence_no: 3353,
      })
        .then((response) => {
          this.loading = false;
          console.log("pfcp:", response);
          if ((response != null) & (response != [])) {
            this.serverData = response;
          }
        })
        .catch((e) => {
          this.loading = false;
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
  },
};
</script>


